/**
 * 最外层包裹的home组件，包括
 */
import React, { PureComponent } from 'react';
import BasicLayout from 'Layouts/SparepartsManage';
import {
  Route, Redirect,
} from 'react-router-dom';
import {
  DataPage, PerformancePage, DevicePage
} from './configs'

class SparepartsRouter extends PureComponent {
  render() {
    return <BasicLayout children={<React.Fragment>
      <Route exact path="/statistics" render={() => <Redirect to='/statistics/data' />} />
      <Route path='/statistics/data' component={DataPage} />
      <Route path='/statistics/performance' component={PerformancePage} />
      <Route path='/statistics/device' component={DevicePage} />
    </React.Fragment>} />;
  }

}
export default SparepartsRouter;